@import url('https://fonts.googleapis.com/css2?family=Inter:ital,opsz,wght@0,14..32,100..900;1,14..32,100..900&display=swap');

:root {
    font-size: 62.5%;
    --fast-duration: 0.15s;
    --slow-duration: 0.25s;
    --easing: cubic-bezier(0.4, 0, 0.2, 1);
}

[data-theme='dark'] {
    --bg-1: #f0f0f0;
    --bg-2: #303030;
    --bg-3: #1a1a1a;
    --bg-4: #151515;
    --fg-1: #ffffff;
    --fg-2: #575757;
    --main-color: #ff3e39;
}

[data-theme='light'] {
    --bg-1: #f0f0f0;
    --bg-2: #ebebeb;
    --bg-3: #ffffff;
    --bg-4: #fafafa;
    --fg-1: #0a0a0a;
    --fg-2: #bdbdbd;
    --main-color: #ff3e39;
}

* {
    font-family: 'Inter', sans-serif;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    border: none;
    outline: none;
}

body {
    width: 100dvw;
    height: 100dvh;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    background: var(--bg-1);
}

::selection {
    background: #0080ff80;
    color: var(--fg-1);
}

@keyframes blink {

    from,
    to {
        opacity: 1;
    }

    50% {
        opacity: 0;
    }
}

.calculator {
    font-family: 'Josefin Sans', sans-serif;
    width: 30rem;
    height: 60rem;
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
    --radius: 2.5rem;

    .display {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background: var(--bg-4);
        border-radius: var(--radius) var(--radius) 0 0;

        .items {
            width: 100%;
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            position: relative;
            overflow: hidden;
            padding: 1rem;
            --spacing-y: 5rem;

            .item {
                display: flex;
                align-items: center;
                position: absolute;
                font-size: var(--font-size);
                font-weight: var(--font-weight);
                scale: var(--scale, 1);
                transition-property: transform, opacity, scale;
                transition-duration: var(--slow-duration);
                transition-timing-function: var(--easing);

                &.no-opacity {
                    opacity: 0;
                }

                .group {
                    display: flex;
                    transition-property: font-size, font-weight, color, padding;
                    transition-duration: var(--slow-duration);
                    transition-timing-function: var(--easing);

                    &.spaced {
                        --spacing-x: 1;
                        padding: 0 calc(var(--spacing-x, 0) * 1rem);
                    }
                }

                .cursor {
                    width: 0;
                    height: 0;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    position: relative;
                    pointer-events: none;

                    &:before {
                        content: '';
                        position: absolute;
                        width: 0.2rem;
                        height: 4rem;
                        background: var(--fg-1);
                        animation: 1.5s blink step-end infinite;
                    }
                }

                &.previous {
                    transform: translateY(calc(var(--spacing-y) * -1));
                }

                &.current {
                    --font-size: 4rem;
                    --font-weight: 800;
                    padding: 0 1rem;
                    user-select: none;
                    cursor: text;

                    .group {
                        &.digits {
                            color: var(--fg-1);
                        }

                        &.faded {
                            color: var(--fg-2);
                        }

                        &.colored {
                            color: var(--main-color);
                        }
                    }
                }

                &.next {
                    transform: translateY(var(--spacing-y));
                }

                &.previous,
                &.next {
                    --font-size: 1.4rem;
                    --font-weight: 600;
                    user-select: none;

                    .group {
                        color: var(--fg-2) !important;

                        &.colored {
                            font-family: 'Inter', sans-serif;
                        }
                    }
                }
            }
        }
    }

    .keyboard {
        display: flex;
        flex-shrink: 0;
        padding: 3rem;
        background: var(--bg-3);
        border-radius: 0 0 var(--radius) var(--radius);
        user-select: none;

        .keys {
            display: flex;
            flex-wrap: wrap;
            width: 100%;
            --gap: 1rem;
            --items-x: 4;
            gap: var(--gap);

            .key {
                display: flex;
                justify-content: center;
                align-items: center;
                width: calc((100% + var(--gap)) / var(--items-x) - var(--gap));
                aspect-ratio: 1;
                border-radius: 50%;
                transition: background-color var(--fast-duration) var(--easing);

                &.faded {
                    color: var(--fg-2);
                }

                &.colored {
                    font-family: 'Inter', sans-serif;
                    color: var(--main-color);
                }

                &.digit {
                    color: var(--fg-1);
                }

                &.filled {
                    background: var(--main-color);
                    color: var(--fg-1);
                    transition: transform var(--fast-duration) var(--easing);

                    &:hover:not(:active) {
                        cursor: pointer;
                        transform: scale(1.05);
                    }
                }

                &:not(.blank, .filled):hover {
                    cursor: pointer;
                    background-color: color-mix(in srgb, currentColor, transparent 90%);
                }

                .text {
                    font-size: 1.6rem;
                    font-weight: 600;
                }

                svg {
                    width: 1.8rem;
                    height: 1.8rem;
                }
            }
        }
    }
}

.hidden {
    display: none;
}

.temp {
    opacity: 0 !important;
    pointer-events: none !important;
}

.no-transition,
.no-transition * {
    transition: none !important;
    transition-property: none !important;
    transition-duration: 0s !important;
}

.by-nassinger {
    display: flex;
    position: fixed;
    bottom: 0;
    right: 0;
    margin: 2rem;
    font-size: 1.4rem;
    font-weight: 600;
    color: var(--main-color);
}