@import "https://fonts.googleapis.com/css2?family=Inter:ital,opsz,wght@0,14..32,100..900;1,14..32,100..900&display=swap";
:root {
  --fast-duration: .15s;
  --slow-duration: .25s;
  --easing: cubic-bezier(.4, 0, .2, 1);
  font-size: 62.5%;
}

[data-theme="dark"] {
  --bg-1: #f0f0f0;
  --bg-2: #303030;
  --bg-3: #1a1a1a;
  --bg-4: #151515;
  --fg-1: #fff;
  --fg-2: #575757;
  --main-color: #ff3e39;
}

[data-theme="light"] {
  --bg-1: #f0f0f0;
  --bg-2: #ebebeb;
  --bg-3: #fff;
  --bg-4: #fafafa;
  --fg-1: #0a0a0a;
  --fg-2: #bdbdbd;
  --main-color: #ff3e39;
}

* {
  box-sizing: border-box;
  border: none;
  outline: none;
  margin: 0;
  padding: 0;
  font-family: Inter, sans-serif;
}

body {
  background: var(--bg-1);
  justify-content: center;
  align-items: center;
  width: 100dvw;
  height: 100dvh;
  display: flex;
  overflow: hidden;
}

::selection {
  color: var(--fg-1);
  background: #0080ff80;
}

@keyframes blink {
  from, to {
    opacity: 1;
  }

  50% {
    opacity: 0;
  }
}

.calculator {
  --radius: 2.5rem;
  flex-direction: column;
  flex-shrink: 0;
  width: 30rem;
  height: 60rem;
  font-family: Josefin Sans, sans-serif;
  display: flex;

  & .display {
    background: var(--bg-4);
    border-radius: var(--radius) var(--radius) 0 0;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    display: flex;

    & .items {
      --spacing-y: 5rem;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      width: 100%;
      height: 100%;
      padding: 1rem;
      display: flex;
      position: relative;
      overflow: hidden;

      & .item {
        font-size: var(--font-size);
        font-weight: var(--font-weight);
        scale: var(--scale, 1);
        transition-property: transform, opacity, scale;
        transition-duration: var(--slow-duration);
        transition-timing-function: var(--easing);
        align-items: center;
        display: flex;
        position: absolute;

        &.no-opacity {
          opacity: 0;
        }

        & .group {
          transition-property: font-size, font-weight, color, padding;
          transition-duration: var(--slow-duration);
          transition-timing-function: var(--easing);
          display: flex;

          &.spaced {
            --spacing-x: 1;
            padding: 0 calc(var(--spacing-x, 0) * 1rem);
          }
        }

        & .cursor {
          pointer-events: none;
          justify-content: center;
          align-items: center;
          width: 0;
          height: 0;
          display: flex;
          position: relative;

          &:before {
            content: "";
            background: var(--fg-1);
            width: .2rem;
            height: 4rem;
            animation: 1.5s step-end infinite blink;
            position: absolute;
          }
        }

        &.previous {
          transform: translateY(calc(var(--spacing-y) * -1));
        }

        &.current {
          --font-size: 4rem;
          --font-weight: 800;
          user-select: none;
          cursor: text;
          padding: 0 1rem;

          & .group {
            &.digits {
              color: var(--fg-1);
            }

            &.faded {
              color: var(--fg-2);
            }

            &.colored {
              color: var(--main-color);
            }
          }
        }

        &.next {
          transform: translateY(var(--spacing-y));
        }

        &.previous, &.next {
          --font-size: 1.4rem;
          --font-weight: 600;
          user-select: none;

          & .group {
            color: var(--fg-2) !important;

            &.colored {
              font-family: Inter, sans-serif;
            }
          }
        }
      }
    }
  }

  & .keyboard {
    background: var(--bg-3);
    border-radius: 0 0 var(--radius) var(--radius);
    user-select: none;
    flex-shrink: 0;
    padding: 3rem;
    display: flex;

    & .keys {
      --gap: 1rem;
      --items-x: 4;
      gap: var(--gap);
      flex-wrap: wrap;
      width: 100%;
      display: flex;

      & .key {
        width: calc((100% + var(--gap)) / var(--items-x)  - var(--gap));
        aspect-ratio: 1;
        transition: background-color var(--fast-duration) var(--easing);
        border-radius: 50%;
        justify-content: center;
        align-items: center;
        display: flex;

        &.faded {
          color: var(--fg-2);
        }

        &.colored {
          color: var(--main-color);
          font-family: Inter, sans-serif;
        }

        &.digit {
          color: var(--fg-1);
        }

        &.filled {
          background: var(--main-color);
          color: var(--fg-1);
          transition: transform var(--fast-duration) var(--easing);

          &:hover:not(:active) {
            cursor: pointer;
            transform: scale(1.05);
          }
        }

        &:not(.blank, .filled):hover {
          cursor: pointer;
          background-color: color-mix(in srgb, currentColor, transparent 90%);
        }

        & .text {
          font-size: 1.6rem;
          font-weight: 600;
        }

        & svg {
          width: 1.8rem;
          height: 1.8rem;
        }
      }
    }
  }
}

.hidden {
  display: none;
}

.temp {
  opacity: 0 !important;
  pointer-events: none !important;
}

.no-transition, .no-transition * {
  transition: none !important;
}

.by-nassinger {
  color: var(--main-color);
  margin: 2rem;
  font-size: 1.4rem;
  font-weight: 600;
  display: flex;
  position: fixed;
  bottom: 0;
  right: 0;
}
